@import "./_variables";

.info-area {
	height: 100%;
	width: 100%;
	border-radius: 10px;
	box-shadow: 0px 5px 20px rgba(7.5, 11, 73, 0.15);
	padding: 13px 12px 15px 13px;
	position: relative;
	margin-bottom: 11px;

	.head {
		position: relative;
		display: flex;
		flex: row;
		width: 100%;
		height: 18px;

		.clickable-name {
			&:hover {
				cursor: pointer;
				.checked {
					background-color: $primary-hover;
					img {
						opacity: 0.3;
					}
				}
				.unchecked {
					background-color: $tertiary-hover;
				}
				.name {
					text-decoration: underline;
				}
			}
			.name {
				position: relative;
				width: 100%;
				font-weight: 600;
				font-size: 18px;
				top: -20px;
				left: 23px;
			}
		}

		.delete-ingredient {
			position: relative;
			text-align: center;
			font-size: 32px;
			font-weight: 600;
			margin-left: auto;
			float: right;
			top: -17px;
		}
	}
	.body {
		position: relative;
		margin-top: 16px;
		margin-left: 13px !important;
		.ingredient {
			position: relative;
			display: flex;
			height: 28px;
			&:hover {
				cursor: pointer;
			}
			.check-area {
				display: flex;
				&:hover {
					.checked {
						background-color: $primary-hover;
						img {
							opacity: 0.3;
						}
					}
					.unchecked {
						background-color: $tertiary-hover;
					}
					.i-name {
						text-decoration: underline;
					}
				}
				.i-name {
					position: relative;
					width: 100%;
					font-size: 14px;
					left: 8px;
				}
				.assigned {
					position: absolute;
					z-index: 6;
					width: 100%;
					text-align: right;
					font-size: 10px;
					color: $primary;
				}
			}
		}
	}
}
